.container{margin:0 auto;padding:0 20px;box-sizing:content-box}.Event{position:relative}.Event:not(:last-child){margin-bottom:12px;padding-bottom:12px;border-bottom:1px solid rgba(131,131,131,.5)}.Event._target .top .date{color:red}.Event._past{opacity:.5}.Event._past::after{content:"У минулому*";position:absolute;left:50%;top:50%;font-size:16px;line-height:140%;font-weight:400;color:#333;color:red;transform:translate(-50%, -50%)}.Event._today{padding:6px 8px;border:1px solid red;border-radius:4px}.Event .top{display:flex;align-items:center}.Event .top .title{font-size:18px;line-height:140%;font-weight:500;color:#333;margin:0 6px 0 0}.Event .top .leading{margin-right:12px;color:rgba(131,131,131,.5)}.Event .top .date{display:flex;align-items:center}.Event .top .date .icon{display:block;margin-right:4px;color:rgba(131,131,131,.5)}.Event .about{margin-bottom:0}